:host {
  display: block;
  width: 100%;
  min-height: 100vh;
  background: var(--bg-secondary);
}

.student-profile-container {
  max-width: 1000px;
  margin: 0 auto;
  padding: 40px 20px;
  min-height: calc(100vh - 64px);
}

// 프로필 헤더 카드
.profile-header-card {
  display: flex;
  align-items: center;
  gap: 24px;
  background: var(--bg-primary);
  padding: 32px;
  border-radius: 20px;
  box-shadow: 0 4px 20px var(--shadow);
  margin-bottom: 32px;
  border: 1px solid var(--border-color);

  .user-avatar-large {
    width: 80px;
    height: 80px;
    background: #f0f7ff;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #007aff;

    mat-icon {
      font-size: 60px;
      width: 60px;
      height: 60px;
    }
  }

  .user-basic-info {
    h1 {
      font-size: 24px;
      font-weight: 700;
      color: var(--text-primary);
      margin: 0 0 4px 0;
    }

    .user-email {
      font-size: 15px;
      color: var(--text-secondary);
      margin: 0 0 12px 0;
    }

    .user-badges {
      display: flex;
      gap: 8px;

      .badge {
        padding: 4px 12px;
        border-radius: 20px;
        font-size: 12px;
        font-weight: 600;

        &.student-badge {
          background: rgba(0, 122, 255, 0.1);
          color: #007aff;
        }

        &.point-badge {
          background: rgba(255, 149, 0, 0.1);
          color: #ff9500;
        }
      }
    }
  }
}

// 다크모드 - 프로필 헤더 카드
[data-theme="dark"],
.dark-theme {
  .profile-header-card {
    .user-avatar-large {
      background: rgba(96, 165, 250, 0.1);
      color: #60a5fa;
    }

    .user-badges {
      .badge {
        &.student-badge {
          background: rgba(96, 165, 250, 0.2);
          color: #60a5fa;
        }
      }
    }
  }
}

// 탭 메뉴
.profile-tabs {
  display: flex;
  gap: 8px;
  margin-bottom: 24px;
  background: var(--bg-tertiary);
  padding: 6px;
  border-radius: 12px;
  width: fit-content;

  .tab-btn {
    display: flex;
    align-items: center;
    gap: 8px;
    padding: 10px 20px;
    border: none;
    background: transparent;
    border-radius: 8px;
    color: var(--text-secondary);
    font-size: 14px;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.2s ease;
    position: relative;

    mat-icon {
      font-size: 20px;
      width: 20px;
      height: 20px;
    }

    &:hover {
      color: var(--text-primary);
      background: var(--bg-primary);
    }

    &.active {
      background: var(--bg-primary);
      color: var(--accent-primary);
      box-shadow: 0 2px 8px var(--shadow);
    }

    .count-badge {
      background: #ff3b30;
      color: white;
      font-size: 10px;
      padding: 2px 6px;
      border-radius: 10px;
      min-width: 18px;
      text-align: center;
    }
  }
}

// 탭 컨텐츠 공통
.tab-content {
  .section-header {
    margin-bottom: 24px;

    h2 {
      font-size: 20px;
      font-weight: 700;
      color: var(--text-primary);
      margin: 0 0 4px 0;
    }

    p {
      font-size: 14px;
      color: var(--text-secondary);
      margin: 0;
    }
  }
}

// 프로필 수정 섹션
.profile-card {
  background: var(--bg-primary);
  border-radius: 20px;
  padding: 32px;
  box-shadow: 0 4px 20px var(--shadow);
  border: 1px solid var(--border-color);
}

// 수강 과목 섹션
.enrollment-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(450px, 1fr));
  gap: 20px;

  .enrollment-card {
    display: flex;
    background: var(--bg-primary);
    border-radius: 16px;
    overflow: hidden;
    box-shadow: 0 4px 15px var(--shadow);
    border: 1px solid var(--border-color);
    transition: transform 0.2s ease;

    &:hover {
      transform: translateY(-4px);
      box-shadow: 0 8px 25px var(--shadow);
    }

    .course-thumb {
      width: 160px;
      height: 160px;
      flex-shrink: 0;
      background-color: var(--bg-secondary);
      border-radius: 8px;
      display: flex;
      align-items: center;
      justify-content: center;
      overflow: hidden;

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        border-radius: 8px;
      }

      .course-avatar {
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
        color: white;
        font-size: 3rem;
        font-weight: bold;
        border-radius: 8px;
      }
    }

    .enrollment-info {
      flex: 1;
      padding: 20px;
      display: flex;
      flex-direction: column;

      .course-title {
        font-size: 17px;
        font-weight: 700;
        color: #1d1d1f;
        margin-bottom: 4px;
        display: -webkit-box;
        -webkit-line-clamp: 1;
        -webkit-box-orient: vertical;
        overflow: hidden;
      }

      .school-name {
        font-size: 13px;
        color: #007aff;
        font-weight: 600;
        margin-bottom: 16px;
      }

      .progress-container {
        margin-bottom: 16px;

        .progress-label {
          display: flex;
          justify-content: space-between;
          font-size: 12px;
          font-weight: 600;
          color: #86868b;
          margin-bottom: 6px;

          span:last-child {
            color: #007aff;
          }
        }

        ::ng-deep .mat-mdc-progress-bar {
          height: 6px;
          border-radius: 3px;
        }
      }

      .enrollment-footer {
        margin-top: auto;
        display: flex;
        justify-content: space-between;
        align-items: center;

        .start-date {
          font-size: 12px;
          color: #86868b;
        }

        .action-buttons {
          display: flex;
          gap: 8px;

          button {
            border-radius: 8px;
            font-weight: 600;
            display: flex;
            align-items: center;
            gap: 4px;

            mat-icon {
              font-size: 18px;
              width: 18px;
              height: 18px;
            }
          }
        }
      }
    }
  }
}

// 빈 상태
.empty-state {
  text-align: center;
  padding: 60px 20px;
  background: #ffffff;
  border-radius: 20px;
  border: 2px dashed rgba(0, 0, 0, 0.05);

  mat-icon {
    font-size: 48px;
    width: 48px;
    height: 48px;
    color: #d2d2d7;
    margin-bottom: 16px;
  }

  p {
    font-size: 16px;
    color: #86868b;
    margin-bottom: 20px;
  }

  button {
    border-radius: 20px;
    padding: 0 24px;
  }
}

.loading-spinner {
  display: flex;
  justify-content: center;
  padding: 40px;
}

.no-user {
  text-align: center;
  padding: 60px 20px;
  
  mat-icon {
    font-size: 64px;
    width: 64px;
    height: 64px;
    color: #d2d2d7;
    margin-bottom: 16px;
  }
  
  p {
    font-size: 18px;
    font-weight: 600;
    color: #1d1d1f;
    margin: 0 0 8px 0;
    
    &.hint {
      font-size: 14px;
      font-weight: 400;
      color: #86868b;
    }
  }
}

/* 모바일 반응형 */
@media (max-width: 768px) {
  .student-profile-container {
    padding: 20px 16px;
  }

  .profile-header-card {
    padding: 20px;
    flex-direction: column;
    text-align: center;
    gap: 16px;

    .user-avatar-large {
      width: 64px;
      height: 64px;
      mat-icon {
        font-size: 48px;
        width: 48px;
        height: 48px;
      }
    }

    .user-basic-info {
      h1 {
        font-size: 20px;
      }
      .user-badges {
        justify-content: center;
      }
    }
  }

  .profile-tabs {
    width: 100%;
    overflow-x: auto;
    padding: 4px;
    
    .tab-btn {
      flex: 1;
      white-space: nowrap;
      padding: 8px 12px;
      font-size: 13px;
      
      span:not(.count-badge) {
        display: none;
      }
    }
  }

  .enrollment-list {
    grid-template-columns: 1fr;

    .enrollment-card {
      flex-direction: column;

      .course-thumb {
        width: 100%;
        height: 180px;
      }
    }
  }
}
// 입교한 학교 섹션
.schools-section {
  animation: fadeIn 0.3s ease-in;
}

.school-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(400px, 1fr));
  gap: 24px;
  margin-top: 20px;

  @media (max-width: 768px) {
    grid-template-columns: 1fr;
  }
}

.school-card {
  background: #ffffff;
  border: 1px solid #e5e5e5;
  border-radius: 12px;
  padding: 24px;
  transition: all 0.3s ease;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);

  &:hover {
    box-shadow: 0 8px 24px rgba(0, 0, 0, 0.1);
    transform: translateY(-4px);
  }

  .school-header {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    gap: 16px;
    margin-bottom: 20px;
    padding-bottom: 16px;
    border-bottom: 1px solid #f0f0f0;

    .school-info {
      flex: 1;

      h3 {
        font-size: 18px;
        font-weight: 600;
        margin: 0 0 8px 0;
        color: #1d1d1f;
      }

      .school-description {
        font-size: 14px;
        color: #666;
        margin: 4px 0 8px 0;
        line-height: 1.4;
      }

      .course-count {
        display: inline-block;
        font-size: 13px;
        color: #007aff;
        font-weight: 500;
      }
    }

    .school-actions {
      button {
        min-width: 100px;
      }
    }
  }

  .school-courses {
    display: flex;
    flex-direction: column;
    gap: 12px;
  }

  .course-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 12px;
    background: #fafafa;
    border-radius: 8px;
    gap: 12px;

    .course-item-info {
      flex: 1;

      h4 {
        font-size: 14px;
        font-weight: 600;
        margin: 0 0 8px 0;
        color: #1d1d1f;
      }

      .progress-info {
        display: flex;
        align-items: center;
        gap: 8px;
        margin-bottom: 6px;

        mat-progress-bar {
          flex: 1;
          height: 4px;
        }

        .progress-text {
          font-size: 12px;
          font-weight: 600;
          color: #007aff;
          min-width: 30px;
        }
      }

      .start-date {
        font-size: 12px;
        color: #999;
      }
    }

    .course-actions {
      display: flex;
      gap: 4px;
    }
  }
}

// 제출 과제 섹션
.assignments-section {
  .assignments-list {
    display: flex;
    flex-direction: column;
    gap: 20px;

    .assignment-card {
      background: var(--bg-primary);
      border-radius: 16px;
      overflow: hidden;
      border: 1px solid var(--border-color);
      box-shadow: 0 2px 12px var(--shadow);
      transition: all 0.2s ease;

      &:hover {
        box-shadow: 0 4px 20px var(--shadow);
        transform: translateY(-2px);
      }

      .assignment-header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 20px;
        border-bottom: 1px solid var(--border-color);
        background: var(--bg-tertiary);

        .assignment-info {
          display: flex;
          flex-direction: column;
          gap: 4px;

          h3 {
            margin: 0;
            font-size: 16px;
            font-weight: 600;
            color: var(--text-primary);
          }

          .week {
            font-size: 12px;
            color: var(--text-secondary);
          }
        }

        .status-badge {
          display: flex;
          align-items: center;
          gap: 8px;
          padding: 6px 12px;
          border-radius: 20px;
          font-size: 12px;
          font-weight: 600;
          text-transform: uppercase;

          .type-badge {
            background: rgba(255, 255, 255, 0.2);
            padding: 2px 8px;
            border-radius: 10px;
            font-size: 10px;
          }

          &.pending {
            background: var(--warning-color);
            color: white;
          }

          &.submitted, &.completed {
            background: var(--success-color);
            color: white;
          }

          &.graded {
            background: var(--accent-primary);
            color: white;
          }
        }
      }

      .assignment-content {
        padding: 20px;
        border-bottom: 1px solid var(--border-color);

        p {
          margin: 0;
          color: var(--text-primary);
          line-height: 1.6;
        }
      }

      .survey-answers {
        padding: 16px 20px;
        border-bottom: 1px solid var(--border-color);
        background: var(--bg-secondary);

        .answer-item {
          margin-bottom: 8px;
          font-size: 13px;
          color: var(--text-primary);
          line-height: 1.5;

          &:last-child {
            margin-bottom: 0;
          }

          strong {
            color: var(--accent-primary);
          }
        }
      }

      .assignment-footer {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 16px 20px;
        background: var(--bg-secondary);
        font-size: 12px;

        .submit-date {
          color: var(--text-secondary);
        }

        .score {
          font-weight: 600;
          color: var(--accent-primary);
        }
      }

      .feedback {
        padding: 20px;
        background: var(--bg-tertiary);
        border-top: 1px solid var(--border-color);

        strong {
          color: var(--text-primary);
          display: block;
          margin-bottom: 8px;
        }

        p {
          margin: 0;
          color: var(--text-primary);
          line-height: 1.6;
        }
      }
    }
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(8px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}